  export default {
    name: 'vue-pay-keyboard',
    props: {
      highlightColor: {// 高亮颜色
        type: String,
        default: '#ccc'
      },
      pasDigits: {// 密码位数
        type: Number,
        default: 6
      }
    },
    data () {
      return {
        val: [],
        keyList: [
          [1, 2, 3],
          [4, 5, 6],
          [7, 8, 9]
        ]
      }
    },
    methods: {
//      关闭键盘
      close(){
          this.$emit('closeKeyboard')
      },
      inputEnd (e, d) {
        this.unhighLight(e.currentTarget, d)
      },
      // 恢复默认
      unhighLight (ele, d) {
        if (d) {
          ele.style.backgroundColor = '#e8e8e8'
        } else {
          ele.style.backgroundColor = '#fff'
        }

      },
      // 高亮
      highlight (ele, del) {
        if (del) {
          ele.style.backgroundColor = '#fff'
        } else {
          ele.style.backgroundColor = this.highlightColor
        }

      },
      // 用户输入
      inputStart (val, e) {
        if (this.val.length < this.pasDigits) {
          this.val.push(val)
        } else {
          // 密码输入完毕
          this.$emit('pasEnd', this.val.join(''))
        }
        // 设置高亮
        this.highlight(e.currentTarget)
      },
      del (e) {
        if (this.val.length > 0) {
          this.val.pop()
        }
        this.highlight(e.currentTarget, 'del')
      }
    }

  }



