import iHeader from  '../../components/i-header.vue'
import payKeyboard from './chird/pay-keyboard.vue'
export default {
  data(){
    return {
      icon: 0,
      bottomSheet: false,
    }
  },
  methods: {
    closeBottomSheet () {
      this.bottomSheet = false
    },
    openBottomSheet () {
      this.bottomSheet = true
    },
    //      支付方式
    payType(icon){
      this.icon = icon;
    },
    pay(icon){
      if (icon == 0) {
        this.openBottomSheet()
      }
    },
  },
  components: {
    iHeader,
    payKeyboard
  }
}



